}

button{
	width:85px;
	height:44px;
	border:.5px solid #51d0de ;
	background-color:#51d0de;
}
button:hover{
	background-color:#bf4aa8;
	cursor:pointer;
	-webkit-transition:1s;
	transition:1s;
	border-radius:10px 10px;

}

