.log{
	text-align: center;
	width:350px;
	box-sizing: border-box;
	padding:40px;
	background-color:#51d0de;
	position:relative;
	top:115px;
	left:38%;
	opacity:.9;
}
.log h2{
	margin:0 0 20px;
	padding:0px;
	font-size:30px;
	text-transform:uppercase;
	color:#bf4aa8;
}
.log .input-group{
	position:relative;
	width:95%;
	margin-bottom:25px;
}
.log .input-group input{
	height:50px;
	width:100%;
	padding: 0 20px;
	box-sizing: border-box;
	font-size: 18px;
	outline:none;
	border:1px solid black;
	color:#bf4aa8;
}


.sub:hover{
	background-color:#d9d9d9;
	-webkit-transition:.5s;
	transition:.5s;
	border-radius:10px;
	cursor: pointer;
}
